<template>
  <div style="width: 70%; margin-bottom: 1rem; color: #212529; margin: 3rem auto 3rem; ">
    <div class="form-group" >
      <label for="writer"></label>
      <input
        type="text"
        class="form-control"
        id="writer"
        ref="writer"
        placeholder=" "
        v-model="writer"
      />
    </div>
    <div class="form-group">
      <label for="title"></label>
      <input
        type="text"
        class="form-control"
        id="title"
        ref="title"
        placeholder=" "
        v-model="title"
      />
    </div>
    <div class="form-group">
      <label for="content"></label>
      <textarea
        type="text"
        class="form-control"
        id="contnet"
        ref="content"
        placeholder=" "
        v-model="content"
      ></textarea>
    </div>
    <div class="text-center">
      <button
        class="btn btn-primary"
        v-if="type == 'create'"
        @click="checkHandler"
      >
        
      </button>
      <button class="btn btn-primary" v-else @click="checkHandler"></button>
      <button class="btn btn-primary" @click="moveList"></button>
    </div>
  </div>
</template>

<script>
import http from '@/util/http-common';
export default {
  name: 'board-Form',
  props: {
    type: { type: String },
  },
  data: function() {
    return {
      no: '',
      regtime: '',
      writer: '',
      title: '',
      content: '',
    };
  },
  methods: {
    checkHandler() {
      let err = true;
      let msg = '';
      !this.writer &&
        ((msg = ' '),
        (err = false),
        this.$refs.writer.focus());
      err &&
        !this.title &&
        ((msg = ' '), (err = false), this.$refs.title.focus());
      err &&
        !this.content &&
        ((msg = ' '),
        (err = false),
        this.$refs.content.focus());

      if (!err) alert(msg);
      else this.type == 'create' ? this.createHandler() : this.updateHandler();
    },
    createHandler() {
      http
        .post('/notice', {
          notice_userid: this.writer,
          notice_title: this.title,
          notice_content: this.content,
        })
        .then(({ data }) => {
          let msg = '   .';
          if (data === 'success') {
            msg = ' .';
          }
          alert(msg);
          this.moveList();
        })
        .catch(() => {
          alert('   .');
        });
    },
    updateHandler() {
      http
        .put(`/notice/${this.no}`, {
          notice_no: this.no,
          notice_datetime: this.regtime,
          notice_userid: this.writer,
          notice_title: this.title,
          notice_content: this.content,
        })
        .then(({ data }) => {
          let msg = '   .';
          if (data === 'success') {
            msg = ' .';
          }
          alert(msg);
          this.moveList();
        })
        .catch(() => {
          alert('   .');
        });
    },
    moveList() {
      this.$router.push('/notice/noticeList');
    },
  },
  created() {
    if (this.type === 'update') {
      http
        .get(`/notice/${this.$route.query.no}`)
        .then(({ data }) => {
          this.no = data.notice_no;
          this.regtime = data.notice_datetime;
          this.writer = data.notice_userid;
          this.title = data.notice_title;
          this.content = data.notice_content;
        })
        .catch(() => {
          alert(' .');
        });
    }
  },
};
</script>

<style scoped></style>
